	module.exports = {"data":{"markdownRemark":{"html":"<h2>Data Structures</h2>\n<p>Data Structure is a way of collecting and organising data in such a way that we can perform operations on these data in an effective way. Data Structures is about rendering data elements in terms of some relationship, for better organization and storage. For example, we have data player’s name “Virat” and age 26. Here “Virat” is of String data type and 26 is of integer data type.</p>\n<p> We can organize this data as a record like Player record. Now we can collect and store player’s records in a file or database as a data structure. For example: “Dhoni” 30, “Gambhir” 31, “Sehwag” 33</p>\n<p>In simple language, Data Structures are structures programmed to store ordered data, so that various operations can be performed on it easily. It represents the knowledge of data to be organized in memory. It should be designed and implemented in such a way that it reduces the complexity and increases the effieciency.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li><a href=\"http://www.studytonight.com/data-structures/introduction-to-data-structures\">Data Structures</a></li>\n<li><a href=\"http://www.geeksforgeeks.org/data-structures/\">Geek for Geek</a></li>\n<li><a href=\"https://www.tutorialspoint.com/data_structures_algorithms/data_structure_overview.htm\">Tutorials Point</a></li>\n<li><a href=\"http://www.studytonight.com/data-structures/introduction-to-data-structures\">Data Structures</a></li>\n</ul>","fields":{"slug":"/computer-science/data-structures/"},"frontmatter":{"title":"Data Structures"}}},"pathContext":{"slug":"/computer-science/data-structures/"}}

